.spinner {
  width: 100px;
  height: 100px;
  border: 8px solid rgba(0, 0, 0, 0.2);
  border-top: 8px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
